body {
  margin: 0;
  padding: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.App {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  justify-items: center;
  padding: 20px; }

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none; }

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white; }

.App-link {
  color: #61dafb; }

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

.xylophone {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  padding: 10px; }

.sequence {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  padding: 10px; }
  .sequence .sequenceInput {
    font-size: 200%;
    padding: 10px; }
  .sequence .play {
    font-size: 200%;
    padding: 10px;
    background: none;
    border: none;
    outline: none; }

.bar {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  width: 6vw;
  min-width: 55px;
  height: 200px;
  border-radius: 4px;
  margin: 0 5px; }

.bar-size-1 {
  height: 220px; }

.bar-size-2 {
  height: 240px; }

.bar-size-3 {
  height: 260px; }

.bar-size-4 {
  height: 280px; }

.bar-size-5 {
  height: 300px; }

.bar-size-6 {
  height: 320px; }

.bar-size-7 {
  height: 340px; }

.bar-size-8 {
  height: 360px; }

.bar-size-9 {
  height: 380px; }

.bar-size-10 {
  height: 400px; }

.bar-size-11 {
  height: 420px; }

.bar-size-12 {
  height: 440px; }

.bar-color-red {
  background: red; }
  .bar-color-red.bar-touched {
    background: #cc0000; }

.bar-color-orange {
  background: orange; }
  .bar-color-orange.bar-touched {
    background: #cc8400; }

.bar-color-yellow {
  background: yellow; }
  .bar-color-yellow.bar-touched {
    background: #cccc00; }

.bar-color-green {
  background: green; }
  .bar-color-green.bar-touched {
    background: #004d00; }

.bar-color-blue {
  background: royalblue; }
  .bar-color-blue.bar-touched {
    background: #214cce; }

.bar-color-darkblue {
  background: darkblue; }
  .bar-color-darkblue.bar-touched {
    background: #000058; }

.bar-color-violet {
  background: blueviolet; }
  .bar-color-violet.bar-touched {
    background: #701ac0; }

.bar-text {
  height: 50px;
  width: 50px;
  background: white;
  border-radius: 50%;
  line-height: 50px;
  text-align: center; }

